*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@-ms-viewport {
  width: device-width;
}
[tabindex="-1"]:focus {
  outline: 0 !important;
}

body {
  max-width: 99rem;
  padding-right: 1rem;
  padding-left: 1rem;
  margin: 1rem auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 16px;
  line-height: 1.5;
  color: #f3f3f3;
  background: #121212;
  text-rendering: optimizeLegibility;
}

img {
  max-width: 100%;
}

hr {
  border: none;
  border-top: 2px solid #fff;
}

@media (min-width: 42rem) {
  body {
    padding-right: 2rem;
    padding-left: 2rem;
  }
}
@media (min-width: 99rem) {
  body {
    max-width: 100%;
  }
}
h1 {
  font-size: 2.25rem;
  line-height: 2.75rem;
}

h2 {
  font-size: 1.75rem;
  line-height: 2.25rem;
}

h3 {
  font-size: 1.25rem;
  line-height: 1.625rem;
}

h4 {
  font-size: 1rem;
  line-height: 1.375rem;
}

h5 {
  font-size: 0.875rem;
  line-height: 1.125rem;
  letter-spacing: 0.16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  color: #f3f3f3;
}

h1,
h2,
h3,
h4,
h5,
h6,
b,
strong,
th {
  font-weight: 700;
}

label {
  font-weight: 700;
  color: #f3f3f3;
}

fieldset {
  padding: 0;
  margin: 0;
  margin-bottom: 1rem;
  border: 0;
}

legend {
  margin-bottom: 0.5rem;
  font-weight: 700;
}

button,
input[type=submit],
input[type=button],
input[type=checkbox] {
  cursor: pointer;
}

input:not([type=checkbox]),
select {
  display: block;
}

input,
select,
button,
textarea {
  padding: 0.75rem;
  margin-top: 0.375rem;
  margin-right: 0.5rem;
  margin-bottom: 1rem;
  font-family: inherit;
  font-size: inherit;
  font-weight: 400;
  color: #f3f3f3;
  background-color: #272727;
  border: none;
  border-radius: 0;
  outline: none;
}

input,
select,
textarea {
  border-bottom: 2px solid #fff;
}

button,
input[type=submit],
input[type=button] {
  height: 2.5rem;
  padding: 0 1rem;
  font-weight: 700;
  color: #fff;
  background-color: #3c6df0;
  border-color: transparent;
  border-style: solid;
  border-width: 2px;
}

input:not([type=checkbox]),
select,
button,
textarea {
  -webkit-appearance: none;
}

textarea {
  box-sizing: border-box;
  width: 100%;
  margin-right: 0;
  resize: vertical;
}

button:hover,
input[type=submit]:hover,
input[type=button]:hover {
  background: #2e3f8f;
}

button:focus,
input[type=submit]:focus,
input[type=button]:focus {
  border: 2px solid #121212;
  outline: 2px solid #3151b7;
}

input:focus,
select:focus,
textarea:focus {
  outline: 2px solid #3151b7;
}

input:disabled,
select:disabled,
textarea:disabled {
  cursor: not-allowed;
  background: #272727;
  border-color: #343334;
}

::placeholder {
  color: #949394;
}

input:disabled::placeholder {
  color: #343334;
}

input[type=checkbox] + label {
  font-weight: 400;
}

a {
  font-weight: 700;
  color: #648fff;
  text-decoration: none;
}

a:hover,
a:active {
  text-decoration: underline;
}

a:visited {
  color: #9b82f3;
}

code,
kbd {
  padding: 5px;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  color: #f3f3f3;
  background: #272727;
  border-radius: 0;
}

pre > code {
  display: block;
  padding: 10px;
  overflow-x: auto;
}

table {
  width: 100%;
  margin-bottom: 1rem;
  border-spacing: 0;
  border-collapse: collapse;
  border: 0;
}

thead {
  font-weight: 700;
  color: #f3f3f3;
  border-bottom: 2px solid #fff;
}

tbody {
  font-weight: 400;
}

td,
th {
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  text-align: left;
}

th:first-of-type,
td:first-of-type {
  padding-left: 1rem;
}

table,
th,
td {
  border: 0;
}

th,
tr {
  height: 3rem;
}

td {
  color: #f3f3f3;
  border-bottom: 1px solid #343334;
}

tr th {
  background: #343334;
}

tr th:hover {
  background: #595859;
}

tbody tr {
  background: #272727;
}

tbody tr:hover {
  background: #343334;
}

ul {
  list-style-type: square;
}